@import url(https://fonts.googleapis.com/css2?family=Staatliches&display=swap);
:root {
    --background: #576675;
    --font: #ECEBEB;
    --cardColour: #4D7D8C;
    --header: #303840;
    --buttonHolder: #4E595C;

}
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
body {
    background-color: var(--background);
}

main {
    min-height: calc(100vh - 112px - 112px);
}
body {
    color: var(--font);
    font-family: 'Staatliches', sans-serif;
}

a {
    text-decoration: none;
    color: var(--font);
}
a:hover{
    text-decoration: underline;
}
.title {
    text-align: center;
    font-size: 5rem;
    padding: 1rem;
}
.headerBG {
    background-color: var(--header);
}

.subTitle {
    text-align: center;
    font-size: 2.5rem;
    padding: 2rem;
}

h2 {
    text-align: center;
    font-size: 3rem;
}

ul {
    font-size: 2.5rem;
    line-height: 4rem;
    padding: 1rem;
}

h3 {
    font-size: 2.5rem;
}

.cardContainer {
    display: grid;
    border-radius: 10px; 
    background-color: var(--cardColour);
    margin: auto;
    margin-bottom: 2rem;
    text-align: center;
    width: 90%;
    box-shadow: 2px 7px 19px 0px rgba(0,0,0,0.51); 
    -webkit-box-shadow: 2px 7px 19px 0px rgba(0,0,0,0.51);
    -moz-box-shadow: 2px 7px 19px 0px rgba(0,0,0,0.51);
}

.cardHeader {
    background-color: var(--header);
    border-radius: 10px 10px 0 0;
}

/* score cards */
.scoreBg {
    background-color: var(--buttonHolder);
    font-size: calc(100px + 1.5vw);
    width: 45%;
    margin: auto;
    margin-top: 2rem;
    margin-bottom: 2rem;
    border-radius: 10px;
}

.cardDivider {
    width: 80%;
    background: linear-gradient(90deg, var(--background), var(--buttonHolder) 20% 80%, var(--background));
    height: 2px;
    margin: auto;
    margin-bottom: 3rem;
}


/* portrait tablets, portrait iPad, landscape e-readers, landscape 800x480 or 854x480 phones */
@media (min-width:641px) {
    .scoreBg {
        width: 30%;
    }
}

/* tablet, landscape iPad, lo-res laptops and desktops */
@media (min-width:961px) {
    .cardGrid{
        display: grid;
        grid-template-columns: repeat(2, 1fr);
    }
}

/* big landscape tablets, laptops, and desktops */
@media (min-width:1025px) {
    .cardContainer {
        width: 70%;
    }
    .infoContainer {
        width: 50%;
    }
}
input {
    background-color: var(--font);
    color: var(--header);
    margin-top: 1rem;
    margin-bottom: 2rem;
    border: none;
    font-size: calc(12px + 1.5vw);
    font-family: 'Staatliches', sans-serif;
    text-align: center;
}

.inputLabel {
    display: block;
    font-size: 2rem;
}

.playerLabel {
    margin-top: 2rem;
}

/* tablet, landscape iPad, lo-res laptops and desktops */
@media (min-width:961px) {
    .inputContainer{
        display: grid;
        grid-template-columns: repeat(2, 1fr);
    }
}
.btn {
    background-color: var(--header);
    border: none;
    border-radius: 20px;
    color: var(--font);
    font-family: 'Staatliches', sans-serif;
    text-align: center;
    padding: .5rem 2rem;
    font-size: calc(12px + 1.5vw);
}
.btnSettings {
    display: flex;
    margin: auto;
    margin-bottom: 2rem;
}
button {
    cursor: pointer;
}
button:hover {
    box-shadow: 0 0 10px #fff inset;
}

.add-btn {
    display: flex;
    margin: auto;
    margin-bottom: 2rem;
}

.generateBtn {
    display: flex;
    margin: auto;
    margin-bottom: 2rem;
}
.confirmBtn {
    margin-bottom: 2rem;
}

.plusBtn {
    margin-bottom: 2rem;
}
footer {
    background-color: var(--header);
    width: 100%;
    height: 7rem;
    display: flex;
    align-items: center;
    justify-content: center;
}
img {
    width: 4rem;
    height: 4rem;
}

